<template>
  <div class="management mescroll">
    <div class="mid">
      <div class="headPic">
        <img class="pic" :src="shopDetail.shopHeader?$utils.dealImg(shopDetail.shopHeader):require('@/assets/img/default/defaultH.png')" alt />
        <img class="icon" src="@/assets/img/merchantsIcon/edit.png" @click="upload('2')" alt />
        <input ref="upload2" type="file" accept="image/*" class="file" style="display:none" @change="chooseUpload" />
      </div>
      <van-cell-group>
        <van-field v-model="shopDetail.shopName" input-align="right" clearable label="商户名" placeholder="请输入您的店名" @blur="blur()" />
        <van-field v-model="shopDetail.shopMobile" input-align="right" clearable type="number" label="联系电话" placeholder="请输入您的客服电话" @blur="blur()" />
        <van-cell title="所在地区" is-link :value="shopDetail.areaName||'请选择您所在的省市区'" @click="showAddress = true" />
        <van-field autosize :rows="2" v-model="shopDetail.shopAddr" input-align="right" label="详细地址" placeholder="请输入您的店址" type="textarea" clearable maxlength="50" show-word-limit @blur="blur()" />
        <van-field autosize :rows="2" v-model="shopDetail.shopDes" input-align="right" label="简介" placeholder="请输入50字以内的简介" type="textarea" clearable maxlength="50" show-word-limit @blur="blur()" />
        <div>
          <van-cell @click="upload('1')">
            <template slot="title">
              <span class="custom-text">详细介绍</span>
            </template>
            <div class="content">
              <p>请上传您的店铺宣传照片</p>
              <img class="icon" src="@/assets/img/merchantsIcon/fujian.png" alt />
              <input ref="upload1" type="file" accept="image/*" class="file" style="display:none" @change="chooseUpload" />
            </div>
          </van-cell>
          <div class="filesList" v-if="picList.length">
            <div v-for="(item,index) in picList" :key="index" class="filesItem">
              <img :src="$utils.dealImg(item)" alt />
              <span class="del" @click="delImg(index)">×</span>
            </div>
          </div>
        </div>
      </van-cell-group>
      <div class="submit-wrap">
        <div class="submit" @click="submit()">提交</div>
      </div>
    </div>
    <van-popup v-model="showAddress" position="bottom">
      <van-area :area-list="areaList" :value="shopDetail.areaCode" @confirm="areaConfirm" @cancel="showAddress = false" />
    </van-popup>
  </div>
</template>
<script>
import { CellGroup, Cell, Field, Popup, Area } from "vant";
import areaList from "@/utils/AreaList";
import {
  getMerchantsDetail,
  saveMerchantsDetail,
  saveNewMerchants
} from "@/api";
export default {
  components: {
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Field.name]: Field,
    [Popup.name]: Popup,
    [Area.name]: Area
  },
  data() {
    return {
      shopDetail: {
        shopHeader: "",
        shopName: "",
        shopMobile: "",
        areaName: "",
        areaCode: "",
        shopMsg: "",
        shopAddr: "",
        shopDes: ""
      },
      type: "",
      areaName: "",
      areaList: areaList,
      showAddress: false,
      provinceId: "",
      cityId: "",
      countyId: "",
      address: "",
      picList: []
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    areaConfirm(obj) {
      console.log(obj);
      this.showAddress = false;
      this.shopDetail.areaName = `${obj[0].name}/${obj[1].name}/${obj[2].name}`;
      this.shopDetail.areaCode = obj[2].code;
    },
    delImg(index) {
      this.picList.splice(index, 1);
    },
    upload(type) {
      this.type = type;
      if (this.type == 1 && this.picList.length > 5) {
        this.$toast("最多只能上传5张图片");
        return;
      }
      this.$refs["upload" + this.type].click();
    },
    chooseUpload(e) {
      let that = this,
        formData = new FormData(),
        files = e.target.files,
        reader = new FileReader();
      if (files.length < 1) {
        return;
      }
      this.loading = this.$toast.loading({
        duration: 0,
        forbidClick: true,
        message: "加载中..."
      });
      for (let i = 0; i < files.length; i++) {
        console.log(files[i]);
        let size = files[i].size / 1024 / 1024;
        if (size > 100) {
          // 超过2M
          this.$toast.fail("文件大于100M");
          return;
        }
        reader.readAsDataURL(files[i]);
        reader.onload = function() {
          let content = this.result; //图片的src，base64格式
          let img = new Image();
          img.src = content;
          img.onload = function() {
            //图片加载完毕
            that.compressEvent(img, size, dataURL => {
              that.uploadImg(dataURL, files[i], i, files.length);
            });
          };
        };
      }
    },
    compressEvent(img, size, callback) {
      let that = this,
        canvas = document.createElement("canvas"),
        ctx = canvas.getContext("2d"),
        initSize = img.src.length,
        width = img.width,
        height = img.height;
      // var ratio = this.getPixelRatio(ctx);
      // console.log(width * ratio, height * ratio);
      canvas.width = width;
      canvas.height = height;
      ctx.drawImage(img, 0, 0, width, height);
      let dataURL = canvas.toDataURL("image/jpeg", size > 1 ? 0.1 : 1);
      callback ? callback(dataURL) : null; //调用回调函数
    },
    getPixelRatio(context) {
      var backingStore =
        context.backingStorePixelRatio ||
        context.webkitBackingStorePixelRatio ||
        context.mozBackingStorePixelRatio ||
        context.msBackingStorePixelRatio ||
        context.oBackingStorePixelRatio ||
        context.backingStorePixelRatio ||
        1;
      return (window.devicePixelRatio || 1) / backingStore;
    },
    dataURItoBlob(dataurl) {
      //dataurl是base64格式
      var arr = dataurl.split(","),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], { type: mime });
    },
    uploadImg(dataURL, file, index, length) {
      // let size = file.size / 1024 / 1024;
      // if (size > 50) {
      //   // 超过2M
      //   this.$toast.fail("文件大于50M");
      //   this.loading.clear();
      //   return;
      // }
      let blob = this.dataURItoBlob(dataURL),
        formData = new FormData(),
        config = {
          headers: { "Content-Type": "multipart/form-data" }
        };
      formData.append("file", blob, file.name);
      formData.append("path", 1);
      this.$axios
        .post(
          this.$config.host + "/shichuang/frame/shop/upload/pic",
          formData,
          config
        )
        .then(res => {
          if (index + 1 == length) {
            this.loading.clear();
          }
          console.log(res);
          if (res.code == 0) {
            if (this.type == "1") {
              this.picList.push(res.data.filePath);
            } else {
              this.shopDetail.shopHeader = res.data.filePath;
            }
          } else {
            this.$toast.fail("图片上传失败！");
          }
        })
        .catch(err => {
          this.$toast.clear();
          console.log(err);
          this.$toast.fail("网络错误");
        });
    },
    submit() {
      if (!this.shopDetail.shopName) {
        this.$toast("请输入商户名");
        return;
      }
      if (!this.shopDetail.shopMobile) {
        this.$toast("请输入联系电话");
        return;
      }
      if (!this.$utils.isMobile(this.shopDetail.shopMobile)) {
        this.$toast("手机号码格式不正确");
        return;
      }
      if (!this.shopDetail.areaCode) {
        this.$toast("请选择所在地区");
        return;
      }
      if (!this.shopDetail.shopAddr) {
        this.$toast("请输入详细地址");
        return;
      }

      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      let dataObj = {
        shopHeader: this.shopDetail.shopHeader,
        shopName: this.shopDetail.shopName,
        shopMobile: this.shopDetail.shopMobile,
        areaCode: this.shopDetail.areaCode,
        shopAddr: this.shopDetail.shopAddr,
        shopDes: this.shopDetail.shopDes,
        shopMsg: this.picList.join(",")
      };
      if (this.$route.query.type == "1") {
        saveNewMerchants(dataObj).then(res => {
          loading.clear();
          if (res.code == 0) {
            this.$toast("新增成功");
            setTimeout(() => {
              this.$router.replace({
                name: "merchantsCenter"
              });
            });
          } else {
            this.$toast(res.msg);
          }
        });
      } else {
        saveMerchantsDetail(dataObj).then(res => {
          loading.clear();
          if (res.code == 0) {
            this.$toast("修改成功");
            setTimeout(() => {
              this.$router.back();
            });
          } else {
            this.$toast(res.msg);
          }
        });
      }
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getMerchantsDetail().then(res => {
        loading.clear();
        if (res.code == 0) {
          this.shopDetail = {
            shopHeader: res.data.shop.shopHeader,
            shopName: res.data.shop.shopName,
            shopMobile: res.data.shop.shopMobile,
            areaName: res.data.shop.areaName,
            areaCode: res.data.shop.areaCode,
            shopMsg: res.data.shop.shopMsg,
            shopAddr: res.data.shop.shopAddr,
            shopDes: res.data.shop.shopDes
          };
          // this.picList = this.shopDetail.shopMsg
          //   ? this.shopDetail.shopMsg.split(",")
          //   : [];
        } else {
          this.$toast(res.msg);
        }
      });
    }
  },
  mounted() {
    if (!this.$route.query.type) {
      this.loadData();
    } else {
      document.title = "我要入驻";
    }
  }
};
</script>
<style lang="scss" scoped>
.management {
  .mid {
    background: #fff;
    padding: 10px 0;
    .headPic {
      width: 200px;
      margin: 5px auto 15px;
      position: relative;
      .pic {
        width: 200px;
        max-height: 150px;
      }
      .icon {
        position: absolute;
        width: 25px;
        height: 25px;
        right: 0;
        bottom: 0;
      }
    }
    .content {
      display: flex;
      align-items: center;
      p {
        color: #333;
        flex: 2;
      }
      .icon {
        width: 20px;
        height: 20px;
        margin-left: 5px;
      }
    }
    .filesList {
      padding: 10px 15px;
      background: #fff;
      .filesItem {
        position: relative;
        border-bottom: 1pt solid #efefef;
        width: 30%;
        display: inline-block;
        margin-right: 3%;
        img {
          width: 100%;
        }
        .del {
          position: absolute;
          border: 1pt solid red;
          color: red;
          border-radius: 50%;
          width: 15px;
          height: 15px;
          text-align: center;
          line-height: 15px;
          right: -8px;
          top: -8px;
        }
      }
      p {
        word-break: break-all;
        line-height: 30px;
        margin: 0 30px 0 0;
      }
      .del {
        position: absolute;
        border: 1pt solid red;
        color: red;
        border-radius: 50%;
        width: 15px;
        height: 15px;
        text-align: center;
        line-height: 15px;
        right: 0;
        top: 5px;
      }
    }
    .submit {
      width: 90%;
      margin: 10px auto 0;
      text-align: center;
      background: $base-color;
      color: white;
      height: 45px;
      line-height: 45px;
      border-radius: 10px;
      margin-bottom: 10px;
    }
  }
}
</style>
<style lang="scss">
.management {
  .van-picker__cancel,
  .van-picker__confirm {
    color: #1989fa !important;
  }
  .van-cell__value {
    flex: 2;
  }
}
</style>